.App {
  /*text-align: center;*/
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.space{
  width: 50px;
  height: 100px;
}

.sidebar{
  list-style-type: none;
  margin: 0;
  padding: 0;
  background-color: #f1f1f1;
  position: absolute;
  height: 100%;
  display: block;
}


.list-group-item{
  padding: 20px;
}
.navbar{
  background-color: steelblue !important;
}

.card{
  margin: 20px;
  height: 250px;
  width: 250px;
  border-color: dimgrey;

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
